.road {
  height: 1200px;
  width: 500px;
  background-color: #828282;
  border-right: 20px solid #fff;
  border-left: 20px solid #fff;
  display: flex;
  justify-content: center;
  animation: road 2s linear 10 forwards;
  &::after {
    content: '';
    height: 100%;
    width: 0;
    border: 10px dashed #fff;
  }
}