$color_police: #1b100d;

$color_window: #415765;

.police {
  @extend %center;
  left: 300px;
  width: 110px;
  height: 205px;
  background-color: $color_police;
  position: absolute;
  border: 5px solid #000;
  border-radius: 20px;
  border-top-right-radius: 40px;
  border-top-left-radius: 40px;
  overflow: hidden;
  animation: police 20s linear both;
  &__text {
    position: absolute;
    color: #fff;
    text-transform: uppercase;
    transform: rotate(180deg);
    font-size: 10px;
    left: 30px;
  }
  &::before {
    @extend %back_headlight;
    bottom: 5px;
    left: 5px;
    transform: rotate(30deg);
  }
  &::after {
    @extend %back_headlight;
    bottom: 5px;
    right: 5px;
    transform: rotate(-30deg);
  }
  &__window {
    @extend %center;
    height: 140px;
    width: 80px;
    background-color: $color_window;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 30px;
    border-top-left-radius: 35px;
    border-top-right-radius: 35px;
    &::before {
      content: '';
      display: block;
      width: 30px;
      height: 5px;
      background-color: $color_police;
      position: absolute;
      top: 20px;
      left: -5px;
      transform: rotate(55deg)
    }
    &::after {
      content: '';
      display: block;
      width: 30px;
      height: 5px;
      background-color: $color_police;
      position: absolute;
      top: 17px;
      right: -5px;
      transform: rotate(-55deg)
    }
    .police__roof {
      @extend %center;
      display: block;
      width: 70px;
      height: 125px;
      background-color: #fff;
      bottom: -10px;
      border-radius: 40px;
      &::after {
        content: '';
        @extend %center;
        display: block;
        width: 50px;
        height: 15px;
        background-color: $color_window;
        bottom: 15px;
        border-radius: 50px;
      }
      .nine {
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        text-align: center;
        margin-top: 60px;
      }
      .flasher {
        @extend %center;
        top: 30px;
        width: 60px;
        height: 20px;
        background-color: $color_police;
        border-radius: 50px;
        &::before {
          content: '';
          position: absolute;
          left: 0px;
          width: 20px;
          height: 20px;
          border-top-left-radius: 50px;
          border-bottom-left-radius: 50px;
          background-color: #1a51cf;
          animation: left 0.5s linear infinite;
        }
        &::after {
          content: '';
          position: absolute;
          right: 0px;
          width: 20px;
          height: 20px;
          border-top-right-radius: 50px;
          border-bottom-right-radius: 50px;
          background-color: #d11718;
          animation: right 0.5s linear infinite;
        }
      }
    }
  }
  .front_headlight {
    @extend %front_headlight;
    background-color: #fbde50;
    &::after {
      content: '';
      @extend %front_headlight;
      display: block;
      background-color: #fbde50;
      position: absolute;
      right: 0;
    }
  }
}