@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: burlywood;
}

.container {
  width: 1200px;
  height: 600px;
  margin: 100px auto;
  background-color: #804231;
  border: 20px solid black;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

//speedo

%big_line {
  width: 40px;
  height: 5px;
  background: black;
  position: absolute;
}

%small_line {
  content: '';
  display: block;
  width: 20px;
  height: 5px;
  background: black;
  position: absolute;
}

%number {
  font-family: 'Orbitron', sans-serif;
  position: absolute;
  top: -10px;
}

//cars

%center {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
}

%back_headlight {
  content: '';
  display: block;
  height: 5px;
  width: 15px;
  border-radius: 5px;
  background-color: red;
  position: absolute;
}

%front_headlight {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}