.speedo {
  width: 400px;
  height: 400px;
  background: linear-gradient(30deg, black, #ff0000ab);
  border: 5px solid #350000;
  border-radius: 50%;
  position: absolute;
  bottom: -200px;
  right: -170px;
  overflow: hidden;
  &__0 {
    @extend %big_line;
    top: 185px;
    transform: rotate(2deg);
    &::after {
      @extend %small_line;
      top: -15px;
      transform: rotate(4deg);
    }
    &::before {
      @extend %number;
      content: '0';
      transform: rotate(-2deg);
      right: -15px;
      top: -10px
    }
  }
  &__20 {
    @extend %big_line;
    top: 155px;
    left: 3px;
    transform: rotate(10deg);
    &::after {
      @extend %small_line;
      top: -16px;
      left: 2px;
      transform: rotate(4deg);
    }
    &::before {
      @extend %number;
      content: '20';
      transform: rotate(-12deg);
      right: -30px;
    }
  }
  &__40 {
    @extend %big_line;
    top: 125px;
    left: 10px;
    transform: rotate(20deg);
    &::after {
      @extend %small_line;
      top: -16px;
      left: 4px;
      transform: rotate(4deg);
    }
    &::before {
      @extend %number;
      content: '40';
      transform: rotate(-20deg);
      right: -30px;
    }
  }
  &__60 {
    @extend %big_line;
    top: 95px;
    left: 25px;
    transform: rotate(30deg);
    &::after {
      @extend %small_line;
      top: -16px;
      left: 4px;
      transform: rotate(6deg);
    }
    &::before {
      @extend %number;
      content: '60';
      transform: rotate(-30deg);
      right: -30px;
    }
  }
  &__80 {
    @extend %big_line;
    top: 68px;
    left: 45px;
    transform: rotate(40deg);
    &::after {
      @extend %small_line;
      top: -16px;
      left: 5px;
      transform: rotate(6deg);
    }
    &::before {
      @extend %number;
      content: '80';
      transform: rotate(-40deg);
      right: -30px;
    }
  }
  &__100 {
    @extend %big_line;
    top: 45px;
    left: 70px;
    transform: rotate(50deg);
    &::after {
      @extend %small_line;
      top: -18px;
      left: 6px;
      transform: rotate(6deg);
    }
    &::before {
      @extend %number;
      content: '100';
      transform: rotate(-50deg);
      right: -30px;
    }
  }
  &__120 {
    @extend %big_line;
    top: 25px;
    left: 100px;
    transform: rotate(65deg);
    &::after {
      @extend %small_line;
      top: -20px;
      left: 10px;
      transform: rotate(6deg);
    }
    &::before {
      @extend %number;
      content: '120';
      transform: rotate(-65deg);
      right: -30px;
    }
  }
  &__140 {
    @extend %big_line;
    top: 15px;
    left: 140px;
    transform: rotate(80deg);
    &::after {
      @extend %small_line;
      top: -20px;
      left: 5px;
      transform: rotate(6deg);
    }
    &::before {
      @extend %number;
      content: '140';
      transform: rotate(-80deg);
      right: -30px;
    }
  }
  &__160 {
    @extend %big_line;
    top: 13px;
    left: 180px;
    transform: rotate(90deg);
    &::after {
      @extend %small_line;
      top: -20px;
      left: 0px;
      transform: rotate(6deg);
    }
    &::before {
      @extend %number;
      content: '160';
      transform: rotate(-90deg);
      right: -30px;
    }
  }
  .arrow {
    width: 400px;
    height: 5px;
    background: #bd1abd;
    position: absolute;
    top: 50%;
    bottom: 50%;
    left: 0px;
    right: 0;
    margin: 0 auto;
    animation: arrow 20s linear;
  }
}