//animation

@keyframes road {
  0% {
    transform: translateY(0);
  }
  100% {
  transform: translateY(640px);
  }
}

@keyframes arrow {
  0% {
    transform: rotate(0);
  }
  10% {
    transform: rotate(20deg);
  }
  20% {
    transform: rotate(60deg);
  }
  30% {
    transform: rotate(70deg);
  }
  40% {
    transform: rotate(80deg);
  }
  50% {
    transform: rotate(90deg);
  }
  60% {
    transform: rotate(60deg);
  }
  70% {
    transform: rotate(40deg);
  }
  80% {
    transform: rotate(60deg);
  }
  90% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(0);
  }
}

@keyframes car {
  0% {
    transform: translate(0px, 200px);
  }
  10% {
    transform: translate(-50px, 100px) rotate(-10deg);
  }
  20% {
    transform: translate(-50px, -150px) rotate(0deg);
  }
  30% {
    transform: translate(-50, -200px) rotate(10deg);
  }
  40% {
    transform: translate(-100px, -300px) rotate(-10deg);
  }
  50% {
    transform: translate(-50px, -350px) rotate(0deg);
  }
  60% {
    transform: translate(-100px, -250px) rotate(10deg);
  }
  70% {
    transform: translate(-100px, -100px) rotate(-10deg);
  }
  80% {
    transform: translate(-100px, -200px) rotate(10deg);
  }
  90% {
    transform: rotate(-120deg);
  }
  100% {
    transform: translateX(-100px) rotate(-120deg);
  }
}

@keyframes police {
  0% {
    transform: translate(0px, 250px);
  }
  10% {
    transform: translate(0px, 200px);
  }
  20% {
    transform: translate(0px, 150px);
  }
  30% {
    transform: translate(0px, 50px);
  }
  40% {
    transform: translate(-50px, -100px) rotate(-5deg);
  }
  50% {
    transform: translate(-80px, -150px);
  }
  60% {
    transform: translate(-80px, -200px);
  }
  70% {
    transform: translate(-60px, -150px);
  }
  80% {
    transform: translate(-80px, -160px) rotate(-30deg);
  }
  90% {
    transform: translate(-150px, -200px) rotate(-75deg);
  }
  100% {
    transform: translate(-180px, -250px) rotate(-100deg);
  }
}

@keyframes left{
  0% {
    background-color: #1a51cf;
  }
  100% {
    background-color: #d11718;
  }
}

@keyframes right{
  0% {
    background-color: #d11718;
  }
  100% {
    background-color: #1a51cf;
  }
}