$color_car: #fbde50;

$color_window: #415765;

.car {
  width: 110px;
  height: 205px;
  background-color: $color_car;
  position: absolute;
  border: 5px solid #000;
  border-radius: 20px;
  border-top-right-radius: 40px;
  border-top-left-radius: 40px;
  overflow: hidden;
  animation: car 20s linear both;
  &::before {
    @extend %back_headlight;
    bottom: 5px;
    left: 5px;
    transform: rotate(30deg);
  }
  &::after {
    @extend %back_headlight;
    bottom: 5px;
    right: 5px;
    transform: rotate(-30deg);
  }
  &__window {
    @extend %center;
    height: 140px;
    width: 80px;
    background-color: $color_window;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 30px;
    border-top-left-radius: 35px;
    border-top-right-radius: 35px;
    &::before {
      content: '';
      display: block;
      width: 30px;
      height: 5px;
      background-color: $color_car;
      position: absolute;
      top: 20px;
      left: -5px;
      transform: rotate(55deg)
    }
    &::after {
      content: '';
      display: block;
      width: 30px;
      height: 5px;
      background-color: $color_car;
      position: absolute;
      top: 20px;
      right: -5px;
      transform: rotate(-55deg)
    }
    .car__roof {
      @extend %center;
      display: block;
      width: 70px;
      height: 125px;
      background-color: $color_car;
      bottom: -10px;
      border-radius: 40px;
      &::after {
        content: '';
        @extend %center;
        display: block;
        width: 50px;
        height: 15px;
        background-color: $color_window;
        bottom: 15px;
        border-radius: 50px;
      }
    }
  }
  .front_headlight {
    @extend %front_headlight;
    background-color: $color_window;
    &::after {
      content: '';
      @extend %front_headlight;
      display: block;
      background-color: $color_window;
      position: absolute;
      right: 0;
    }
  }
}